.cp2-chord-line {
  font-weight: bold;
  white-space: pre-wrap;
  background-color: #e7e7ce; }
  .cp2-chord-line .cp2-bar-separator {
    margin: 0 10px;
    color: #999; }
    .cp2-chord-line .cp2-bar-separator:first-of-type {
      margin-left: 0; }
    .cp2-chord-line .cp2-bar-separator:last-of-type {
      margin-right: 0; }

body {
  font-family: monospace;
  font-size: 14px; }

.form {
  text-align: center;
  margin: 10px;
  font-size: 16px; }

.container {
  line-height: 1.6em;
  display: flex; }

.left {
  flex: 1 1 auto;
  padding: 10px;
  border-right: 2px solid black; }

.right {
  flex: 1 1 auto;
  padding: 10px; }

#editor {
  background-color: blanchedalmond; }
  #editor p {
    margin: 0; }

#editor, #rendered-song {
  border: 1px solid grey;
  padding: 10px; }

#editor-content {
  display: none; }

#rendered-song {
  background-color: beige; }
  #rendered-song p {
    margin: 0; }
